<template>
  <div class="enquiry-type">
    <group>
      <cell title="" @click="$parent.log({name: ''})" v-link="{name:'enquiryAssetTypes', params:{type: 'obligatory_right'}}" is-link class="kt-cell">
        <div class="icon-circle" slot="icon">
          <i class="icon-pano icon-man"></i>
        </div>
        <div slot="after-title" class="kt-cell-content"></div>
      </cell>
    </group>
    <group>
      <cell title="" @click="showAssetManageAlert()" is-link class="kt-cell">
        <div class="icon-circle" slot="icon">
          <i class="icon-pano icon-man"></i>
        </div>
        <div slot="after-title" class="kt-cell-content">()</div>
      </cell>
    </group>
  </div>
</template>

<script>
import Group from 'vux-components/group'
import Cell from 'vux-components/cell'

export default {
  components: {
    Group,
    Cell
  },
  methods: {
    goTo(name) {
      this.$router.go({
        name: name
      })
    },
    showAssetManageAlert() {
      this.$parent.log({
        name: ''
      })
      this.$parent.showAlert({
        content: ''
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  color: #42b983;
}
</style>



