<template src="./template.html"></template>

<script>
import mixin from './mixin'

export default {
  mixins: [mixin],
  methods: {
    updateView() {
      this.visible.small_loan_fin_exp = this.filter.small_loan_shareholder_type !== 'other'
    }
  },
  data() {
    return {
      asset_type: '',
      visible: {
        small_loan_fin_exp: true
          // guarantee: false,
          // small_loan_shareholder_type: false,
          // guarantee_subject: false
      },
      filter: {
        asset_amount: '',
        asset_life: '',
        small_loan_shareholder_type: '',
        small_loan_fin_exp: '',
        small_loan_company_age: '',
        small_loan_asset_type: '',
        small_loan_bad_rate: ''
      },
      fields: [{
        name: '*',
        group: 'group1',
        key: 'asset_amount',
        type: 'input',
        format: 'wy',
        validate: {
          required: true,
          pattern: {
            rule: '/^\\d+$/',
            message: ''
          }
        },
        unit: ''
      }, {
        name: '*',
        group: 'group1',
        key: 'asset_life',
        type: 'input',
        validate: {
          required: true,
          pattern: {
            rule: '/^\\d+$/',
            message: ''
          }
        },
        unit: ''
      }, {
        name: '',
        group: 'group2',
        key: 'small_loan_shareholder_type',
        type: 'select',
        validate: {
          maxlength: 120
        },
        options: [{
          key: 'country_company',
          value: ''
        }, {
          key: 'centre_company',
          value: ''
        }, {
          key: 'public_company',
          value: ''
        }, {
          key: 'other',
          value: ''
        }]
      }, {
        name: '',
        group: 'group2',
        key: 'small_loan_fin_exp',
        type: 'select',
        validate: {
          maxlength: 120
        },
        options: [{
          key: 'A',
          value: 'A'
        }, {
          key: 'B',
          value: 'B'
        }, {
          key: 'C+',
          value: 'C'
        }]
      }, {
        name: '',
        group: 'group2',
        key: 'small_loan_company_age',
        type: 'select',
        validate: {
          maxlength: 120
        },
        options: [{
          key: '<1',
          value: ''
        }, {
          key: '>1',
          value: ''
        }]
      }, {
        name: '*',
        group: 'group2',
        key: 'small_loan_asset_type',
        type: 'select',
        validate: {
          required: true
        },
        options: [{
          key: '0',
          value: ''
        }, {
          key: '1',
          value: ''
        }, {
          key: '2',
          value: ''
        }, {
          key: '3',
          value: ''
        }, {
          key: '4',
          value: ''
        }, {
          key: '5',
          value: ''
        }]
      }, {
        name: '',
        tip: '=/',
        group: 'group2',
        key: 'small_loan_bad_rate',
        type: 'input',
        validate: {
          // required: true,
          pattern: {
            rule: '/^(|\\d+(\\.\\d+)?)$/',
            message: ''
          }
        },
        unit: '%'
      }]

    }
  }
}
</script>



