<template src="./template.html"></template>

<script>
import mixin from './mixin'

export default {
  mixins: [mixin],
  methods: {
    updateView() {
      // this.visible.developer_type = this.filter.rank === 'no_rank'
      // this.visible.guarantee = this.filter.rank === '50top'
      this.visible.trust_party = this.filter.guarantee
    }
  },
  data() {
    return {
      asset_type: '',
      visible: {
        trust_party: false
      },
      filter: {
        asset_amount: '',
        asset_life: '',
        gov_trusty_level: '',
        gov_trusty_income: '',
        gov_trusty_letter: '',
        guarantee: false,
        trust_party: []
      },

      fields: [{
        name: '*',
        group: 'group1',
        key: 'asset_amount',
        type: 'input',
        format: 'wy',
        validate: {
          required: true,
          pattern: {
            rule: '/^\\d+$/',
            message: ''
          }
        },
        unit: ''
      }, {
        name: '*',
        group: 'group1',
        key: 'asset_life',
        type: 'input',
        validate: {
          required: true,
          pattern: {
            rule: '/^\\d+$/',
            message: ''
          }
        },
        unit: ''
      }, {
        name: '',
        group: 'group2',
        key: 'gov_trusty_level',
        type: 'select',
        tip: '',
        validate: {
          maxlength: 120
        },
        options: [{
          key: 'AAA',
          value: 'AAA'
        }, {
          key: 'AA+',
          value: 'AA+'
        }, {
          key: 'AA',
          value: 'AA'
        }, {
          key: 'AA-',
          value: 'AA-'
        }, {
          key: 'no_grade',
          value: ''
        }]
      }, {
        name: '',
        group: 'group2',
        key: 'gov_trusty_income',
        type: 'select',
        tip: '',
        validate: {
          maxlength: 120
        },
        options: [{
          key: '<30',
          value: '30'
        }, {
          key: '31-50',
          value: '31-50'
        }, {
          key: '>51',
          value: '50'
        }]
      }, {
        name: '',
        group: 'group2',
        key: 'gov_trusty_letter',
        type: 'switch',
        validate: {
          maxlength: 120
        }
      }, {
        name: '',
        group: 'group3',
        key: 'guarantee',
        type: 'switch',
        validate: {
          maxlength: 120
        }
      }, {
        name: '',
        group: 'group3',
        key: 'trust_party',
        type: 'checkboxs',
        validate: {
          required: true
        },
        options: [{
          key: 'guarantee_company',
          value: ''
        }, {
          key: 'isurance_company',
          value: ''
        }, {
          key: 'public_company',
          value: ''
        }, {
          key: 'country_company',
          value: ''
        }, {
          key: 'centre_company',
          value: ''
        }, {
          key: 'land_agent',
          value: ''
        }, {
          key: 'other',
          value: ''
        }]
      }]

    }
  }
}
</script>



