<template src="./template.html"></template>

<script>
import mixin from './mixin'

export default {
  mixins: [mixin],
  methods: {
    updateView() {
      this.visible.company_borrow_shareholder_type = !this.filter.company_borrow_on_market
      this.visible.trust_party = this.filter.guarantee
    }
  },
  data() {
    return {
      asset_type: '',
      visible: {
        company_borrow_shareholder_type: false,
        trust_party: false
      },
      filter: {
        asset_amount: '',
        company_borrow_shareholder_type: '',
        company_borrow_on_market: true,
        asset_life: '',
        guarantee: false,
        trust_party: []
      },

      fields: [{
        name: '*',
        group: 'group1',
        key: 'asset_amount',
        type: 'input',
        format: 'wy',
        validate: {
          required: true,
          pattern: {
            rule: '/^\\d+$/',
            message: ''
          }
        },
        unit: ''
      }, {
        name: '*',
        group: 'group1',
        key: 'asset_life',
        type: 'input',
        validate: {
          required: true,
          pattern: {
            rule: '/^\\d+$/',
            message: ''
          }
        },
        unit: ''
      }, {
        name: '',
        group: 'group2',
        key: 'company_borrow_on_market',
        type: 'switch',
        validate: {
          maxlength: 120
        }
      }, {
        name: '',
        group: 'group2',
        key: 'company_borrow_shareholder_type',
        type: 'select',
        validate: {
          maxlength: 120
        },
        options: [{
          key: 'country_company',
          value: ''
        }, {
          key: 'centre_company',
          value: ''
        }, {
          key: 'public_company',
          value: ''
        }, {
          key: 'other',
          value: ''
        }]
      }, {
        name: '',
        group: 'group3',
        key: 'guarantee',
        type: 'switch',
        validate: {
          maxlength: 120
        }
      }, {
        name: '',
        group: 'group3',
        key: 'trust_party',
        type: 'checkboxs',
        validate: {
          required: true
        },
        options: [{
          key: 'guarantee_company',
          value: ''
        }, {
          key: 'isurance_company',
          value: ''
        }, {
          key: 'public_company',
          value: ''
        }, {
          key: 'country_company',
          value: ''
        }, {
          key: 'centre_company',
          value: ''
        }, {
          key: 'land_agent',
          value: ''
        }, {
          key: 'other',
          value: ''
        }]
      }]

    }
  }
}
</script>



