import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import createLogger from 'vuex/logger'

const debug = process.env.NODE_ENV !== 'production'
const state = {
  title: 'PANO',
  enquiryError: 'BD',
  loadingStatus: false,
  assetTypes: [{
    name: '',
    value: 'eslate',
    component: 'enquiryAssetEslate',
    type: 'obligatory_right',
    icon: 'icon-house',
    color: '#a1b5cd'
  }, {
    name: '',
    value: 'government',
    component: 'enquiryAssetGovernment',
    type: 'obligatory_right',
    icon: 'icon-mans',
    color: '#f0c57c'
  }, {
    name: '',
    value: 'enterprise',
    component: 'enquiryAssetEnterprise',
    type: 'obligatory_right',
    icon: 'icon-money',
    color: '#b0d2d1'
  }, {
    name: '',
    value: 'supply_chain',
    component: 'enquiryAssetSupplyChain',
    type: 'obligatory_right',
    icon: 'icon-link',
    color: '#b5bbcb'
  }, {
    name: '',
    value: 'mini_finance',
    component: 'enquiryAssetMiniFinance',
    type: 'obligatory_right',
    icon: 'icon-key',
    color: '#ecbfab'
  }],
  toast: {
    time: 1000,
    type: 'warn',
    text: '',
    show: false
  },
  alert: {
    show: false,
    title: '',
    buttonText: '',
    content: ''
  },
  user: {
    mobile: '',
    name: '',
    password: ''
  }
}

Vue.use(Vuex)
Vue.config.debug = debug

const store = new Vuex.Store({
  state,
  mutations,
  // strict: debug,
  plugins: debug ? [createLogger()] : []
})
export default store



