// import Router from 'vue-router'
import store from '../vuex/store'
import {
  logOut,
  showToast
} from '../vuex/actions'

export default [(request, next) => {
  next((res) => {
    if (res.status === 419 || res.status === 401) {
      logOut(store)
    } else if (res.status === 403) {
      showToast(store, {
        text: res.json().error || ''
      })
    } else if (res.status === 500 || res.status === 502) { // 
      showToast(store, {
        text: res.json().error || ''
      })
    }
  })
}]



