<template src="./template.html"></template>

<script>
import mixin from './mixin'

export default {
  mixins: [mixin],
  methods: {
    updateView() {
      this.visible.developer_type = this.filter.developer_order === ''
      this.visible.guarantee = this.filter.developer_order === '50'
      this.visible.trust_party = this.filter.guarantee
    }
  },
  data() {
    return {
      asset_type: '',
      visible: {
        guarantee: false,
        developer_type: false,
        trust_party: false
      },
      filter: {
        asset_amount: '',
        asset_life: '',
        developer_order: '',
        developer_type: '',
        guarantee: false,
        trust_party: []
      },

      fields: [{
        name: '*',
        group: 'group1',
        key: 'asset_amount',
        type: 'input',
        format: 'wy',
        validate: {
          required: true,
          pattern: {
            rule: '/^\\d+$/',
            message: ''
          }
        },
        unit: ''
      }, {
        name: '*',
        group: 'group1',
        key: 'asset_life',
        type: 'input',
        validate: {
          required: true,
          pattern: {
            rule: '/^\\d+$/',
            message: ''
          }
        },
        unit: ''
      }, {
        name: '',
        group: 'group2',
        key: 'developer_order',
        type: 'select',
        validate: {
          maxlength: 120
        },
        options: [{
          key: '50top',
          value: '50'
        }, {
          key: 'no_rank',
          value: ''
        }]
      }, {
        name: '',
        group: 'group2',
        key: 'developer_type',
        type: 'select',
        validate: {
          maxlength: 120
        },
        options: [{
          key: 'country_company',
          value: ''
        }, {
          key: 'centre_company',
          value: ''
        }, {
          key: 'public_company',
          value: ''
        }, {
          key: 'other',
          value: ''
        }]
      }, {
        name: '',
        group: 'group3',
        key: 'guarantee',
        type: 'switch'
      }, {
        name: '',
        group: 'group3',
        key: 'trust_party',
        type: 'checkboxs',
        validate: {
          required: true
        },
        options: [{
          key: 'guarantee_company',
          value: ''
        }, {
          key: 'isurance_company',
          value: ''
        }, {
          key: 'public_company',
          value: ''
        }, {
          key: 'country_company',
          value: ''
        }, {
          key: 'centre_company',
          value: ''
        }, {
          key: 'land_agent',
          value: ''
        }, {
          key: 'other',
          value: ''
        }]
      }]

    }
  }
}
</script>



